import { Cookies } from "@/types/cookies";
import { cookies } from "next/headers";

export default async function getCookies(): Promise<Cookies> {
    const cookieStore = await cookies();
    const response: Cookies = {
        userId: cookieStore.get('userId')?.value,
        userDisplayName: cookieStore.get('userDisplayName')?.value,
        userEmail: cookieStore.get('userEmail')?.value,
        userAvatar: cookieStore.get('userAvatar')?.value,
        accessToken: cookieStore.get('accessToken')?.value,
        refreshToken: cookieStore.get('refreshToken')?.value,
    }
    return response;
}